body {
  font-family: $font-family;
  font-size: $base-font-size;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  min-height: 100%;
  height: 100%;
}

.container {
  width: 95%;
  max-width: 1170px;
  margin: 0 auto;
}

button{
  transition: .2s;
  &:hover{
   opacity: 0.95;
  }
}

$border-radius: 15px;