.footer {
	display: block;
	width: 100%;
	background: $footer-bcg;
	height: 200px;
	margin-top: 15px;

	&__container {
		height: 200px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.aboutMe {
		&__contact {
			color: $color1;
			font-size: 2.5rem;
			border-bottom: 1px solid $color1;
			margin-bottom: 15px;
			text-transform: uppercase;

			@include phone {
				font-size: 2rem;
			}
		}

		&__data {
			margin: 10px 0;

			&__link {
				font-size: 1.2rem;
				transition: 0.2s;
				color: $color1;
				&:hover {
					color: darken($color: $color1, $amount: 8%);
				}

				@include phone {
					font-size: 1rem;
				}
			}
		}

		.fas,
		.fab {
			font-size: 1rem;
			background-color: $color1;
			border-radius: 50%;
			padding: 5px;
			color: $color2;
			margin-right: 5px;
		}
	}
}
